import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import { faArrowsAltV } from '@fortawesome/free-solid-svg-icons'
import { Link } from "react-router-dom";

const Navigation = () => {
    let timesClicked = 0;

    const arrowBtn = (e) => {
        e.preventDefault();
        timesClicked++;

        if (timesClicked % 2 === 0) {
            document.querySelector('.header-nav ul').style.display = "none";
            document.querySelector('.navigate-element').style.marginRight = "5px";
        } else {
            document.querySelector('.header-nav ul').style.display = "block";
            document.querySelector('.navigate-element').style.marginRight = "60px";
        }
    }

    return (
        <div className="navigation">
            <h2 className="title-head"><Link to="/">School Website</Link></h2>
            <li className="navigate-element">
                <p>Navigate to...</p>
                <a href="true" className="arrow" onClick={arrowBtn}><FontAwesomeIcon icon={faArrowsAltV} /></a>
            </li>

            <nav className="header-nav">
                <ul>
                    <li><Link to="/">Home</Link></li>
                    <li><Link to="/about">About Project</Link></li>
                    <li><Link to="/gallery">Gallery</Link></li>
                    <li><Link to="/register">Register</Link></li>
                    <li><Link to="/login">Login</Link></li>
                    <li><Link to="/contact">Contact</Link></li>
                </ul>
            </nav>
        </div>
    );
}

export default Navigation;